<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header"><h3>{{name}}</h3></li>
      <li class="collection-item">Employee ID: {{employee_id}}</li>
      <li class="collection-item">Employee Dept: {{dept}}</li>
      <li class="collection-item">Employee Position: {{position}}</li>
    </ul>
    <router-link to="/" class="btn gray">Back</router-link>
    <button class="btn red" @click="deleteEmployee">Delete</button>
    <div class="fixed-action-btn">
      <router-link v-bind:to="{'name':'edit-employee',params:{employee_id:employee_id}}"
                   class="btn-floating btn-large red">
        <i class="fa fa-pencil"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
  import db from './firebaseinit'

  export default {
    name: "show-employee",
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null,
      }
    },
    beforeRouteEnter(to, from, next) {
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapShot => {
        querySnapShot.forEach(doc => {
          next(vm => {
            vm.employee_id = doc.data().employee_id
            vm.name = doc.data().name
            vm.dept = doc.data().dept
            vm.position = doc.data().position
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapShot => {
          querySnapShot.forEach(doc => {
            this.employee_id = doc.data().employee_id
            this.name = doc.data().name
            this.dept = doc.data().dept
            this.position = doc.data().position
          })
        })
      },
      deleteEmployee() {
        if (confirm('Are you sure?')) {
          db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapShot => {
            querySnapShot.forEach(doc => {
              doc.ref.delete()
              this.$router.push('/')
            })
          })
        }
      }
    }
  }
</script>

<style scoped>

</style>



