<template>
  <div id="edit-employee">
    <h3>Edit Employee</h3>
    <div class="row">
      <form @submit.prevent="updateEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" disabled v-model="employee_id" id="employee-id"/>
            <!--<label for="employee-id">Employee ID</label>-->
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" required v-model="name" id="name"/>
            <!--<label for="name">Employee Name</label>-->
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" required v-model="dept" id="dept"/>
            <!--<label for="dept">Employee Dept</label>-->
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" required v-model="position" id="position"/>
            <!--<label for="position">Employee Position</label>-->
          </div>
        </div>
        <button type="submit" class="btn ">Update</button>
        <router-link to="/" class="btn blue">Back</router-link>
      </form>
    </div>
  </div>
</template>

<script>
  import db from './firebaseinit'

  export default {
    name: "edit-employee",
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null,
      }
    },
    beforeRouteEnter(to, from, next) {
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get().then(querySnapShot => {
        querySnapShot.forEach(doc => {
          next(vm => {
            vm.employee_id = doc.data().employee_id
            vm.name = doc.data().name
            vm.dept = doc.data().dept
            vm.position = doc.data().position
          })
        })
      })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapShot => {
          querySnapShot.forEach(doc => {
            this.employee_id = doc.data().employee_id
            this.name = doc.data().name
            this.dept = doc.data().dept
            this.position = doc.data().position
          })
        })
      },
      updateEmployee() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get().then(querySnapShot => {
          querySnapShot.forEach(doc => {
            doc.ref.update({
              //employee_id: this.employee_id,
              name: this.name,
              dept: this.dept,
              position: this.position,
            }).then(()=>{
              this.$router.push({'name':'show-employee',params:{employee_id:this.employee_id}})
            })
          })
        })
      }
    }
  }
</script>

<style scoped>

</style>



