<template>
  <div id="new-employee">
    <h3>New Employee</h3>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="row">
          <div class="input-field col s12">
            <input type="text" required v-model="employee_id" id="employee-id"/>
            <label for="employee-id">Employee ID</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" required v-model="name" id="name"/>
            <label for="name">Employee Name</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" required v-model="dept" id="dept"/>
            <label for="dept">Employee Dept</label>
          </div>
        </div>
        <div class="row">
          <div class="input-field col s12">
            <input type="text" required v-model="position" id="position"/>
            <label for="position">Employee Position</label>
          </div>
        </div>
        <button type="submit" class="btn ">Submit</button>
        <router-link to="/" class="btn blue">Back</router-link>
      </form>
    </div>
  </div>
</template>

<script>
  import db from './firebaseinit'

  export default {
    name: "new-employee",
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null,
      }
    },
    methods: {
      saveEmployee() {
        db.collection('employees').add({
          employee_id: this.employee_id,
          name: this.name,
          dept: this.dept,
          position: this.position,
        }).then(docRef => {
          this.$router.push('/')
        }).catch(error => {
          console.log(error)
        })
      }
    }
  }
</script>

<style scoped>

</style>



